.container {
  display: flex;
  flex-direction: column;
  margin: 100px 0 0;
  text-align: center;
}

.link {
  font: var(--font-body);
  color: var(--color-text-body);
  line-height: 30px;
}

@media only screen and (min-width: 600px) {
  .container {
    width: 550px;
    margin-left: auto;
    margin-right: auto;
  }
}
